*{
    margin: 0;
    padding: 0;
}
body
{
   background: url(https://thumbs.dreamstime.com/b/glowing-question-marks-glowing-question-marks-computer-generated-d-render-247830152.jpg);
   background-repeat: no-repeat;
   background-size: cover;
   display: flex;
   justify-content: center;
   align-items: center;
   height: 100vh;
   font-family: "Bree Serif", serif;
}
.head
{
    text-align: center;
    color: rgb(248, 244, 7);
    font-size: 20px;
    border-bottom: 50px;
}
.app
{
    width: 95%;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding: 20px;
    display: flex;
    justify-content: space-evenly;
    background: #ff0018;
    background: linear-gradient(90deg,#ff0018 0%, rgba(14,238,42,0.96) 80%);
}
.Question-Section
{
    position: relative ;
    width: 100%;
    color: white;
}
.Question-Section h1 span
{
    color: rgb(9, 185, 255);
    margin-bottom: 10px;
    font-size: 40px;
}
.Question-Text
{
    font-size: 30px;
    margin-top: 10px;
    color: rgb(108, 238, 76);
}
.Answer-Section
{
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: space-between;
    font-family: sans-serif;
}
button
{
    width: 100%;
    font-size: 16px;
    color: white;
    border-radius: 15px;
    display: flex;
    padding: 10px;
    justify-content: flex-start;
    align-items: center;
    border: 3px solid #234668;
    cursor: pointer;
    background: linear-gradient(90deg,#ff0009 0%, #00fff6 80%);
}
.score
{
    display: flex;
    align-items: center;
    font-size: 25px;
    color: white;
    font-weight: bold;
}
